  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #2e2e2e;
  line-height: 1em;
}
@media (max-width: 1279px) {
  .modal__title {
    font-size: 2.5vw;
  }
}

.modal__description {
  font-size: 20px;
  color: #2e2e2e;
}
@media (max-width: 1279px) {
  .modal__description {
    font-size: 1.6vw;
  }
}

.modal__form {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
}

.modal__input {
  width: 100%;
  padding: 15px;
  border: none;
  color: #2e2e2e;
}
.modal__input::placeholder {
  color: #808080;
}

.modal__checkbox-label {
  position: relative;
  width: fit-content;
  padding-left: 35px;
  cursor: pointer;
}
.modal__checkbox-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 45%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #ffffff;
}

.modal__checkbox-input {
  bottom: 20%;
}
.modal__checkbox-input.focus-visible:focus + .modal__checkbox-label {
  outline: 2px solid #b00000;
  outline-offset: 2px;
}

.modal__checkbox-input[type=checkbox]:checked + .modal__checkbox-label::after {
  content: "";
  position: absolute;
  top: calc(50% - 3px);
  left: 3px;
  transform: translateY(-50%) rotate(-50deg);
  width: 15px;
  height: 9px;
  border-bottom: 2px solid #2e2e2e;
  border-left: 2px solid #2e2e2e;
}

.modal__button {
  border-radius: 3px;
}

.full-scroll {
  height: 100%;
  overflow-y: auto;
  scroll-snap-type: y proximity;
  scroll-padding: 100px 0 0 0;
}

.full-scroll__page {
  min-height: 100%;
  scroll-snap-align: start;
}

